import uniIcon from "@dcloudio/uni-ui/lib/uni-icon/uni-icon.vue"
export default {
	components: {uniIcon},
	data() {
		return {
			loginName:"",
			loginPW:"",
			showPassword: true,
		}
	},
	methods: {
		changePassword: function() {
			this.showPassword = !this.showPassword;
		},
		linkLogin: function(link) {
			switch(link){
				case 'qq':
					console.log("1");
					break;
				case 'weixin':
					uni.login({
						provider: link,
						success: (res) => {
							uni.getUserInfo({
								provider: link,
								success: (infoRes) => {
									
								}
							})
						}
					})
					break;
				default:
					console.log("3");
			}
		},
		login: function() {
			uni.request({
				url: this.apiServer + 'userApi/getUserAll',
				method: 'POST',
				data: {
					test:"123"
				},
				header: {
					'content-Type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					console.log(res);
				}
			})
		}
	}
}
